<template>
  <div class="task-page">
    <h2 class="tp__title title">{{ selectedTask.title }}</h2>
    <div class="tp__descr descr">{{ selectedTask.descr }}</div>
  </div>
</template>

<script>
import { useStore } from 'vuex';
export default {
  props: ['id'],
  setup(props) {
    const store = useStore();
    const selectedTask = store.getters.getTasks.find(
      task => task.id === props.id
    );
    return {
      selectedTask
    };
  }
};
</script>
