<template>
  <div class="task-list__btns btns-list ">
    <button class="btn" @click="loadTasks()">Reload</button>
    <router-link class="btn" to="/todo/new-task">Create new task</router-link>
    <button v-if="!isCompleted" class="btn" @click="seeCompleted">
      See done tasks
    </button>
    <button v-else class="btn" @click="seeAll">See tasks to do</button>
  </div>
  <ul class="task-list">
    <the-task
      v-for="task in filteredTasks.list"
      :key="task.id"
      class="task"
      :title="task.title"
      :descr="task.descr"
      :id="task.id"
      :done="task.done"
      @deleteTask="deleteTask"
      @doneTask="completeTask"
    ></the-task>
  </ul>
</template>

<script>
import TheTask from '../tasks/TheTask.vue';
import { reactive, watch, ref } from 'vue';
import { useStore } from 'vuex';
export default {
  components: {
    TheTask
  },
  setup() {
    const store = useStore();
    let tasks = reactive({
      list: []
    });
    loadTasks();

    tasks.list = store.getters.getTasks;

    let isCompleted = ref(false);
    function deleteTask(id) {
      tasks.list = tasks.list.filter(task => task.id != id);
      store.dispatch('deleteTask', id);
    }
    function completeTask(id) {
      tasks.list.find(task => task.id === id).done = true;
      store.dispatch('completeTask', id);
    }
    function seeCompleted() {
      getTasks(true);
      isCompleted.value = true;
    }
    function seeAll() {
      getTasks();
      isCompleted.value = false;
    }
    let filteredTasks = reactive({
      list: []
    });
    function getTasks(getCompleted = false) {
      filteredTasks.list = tasks.list.filter(task => task.done == getCompleted);
    }
    getTasks();

    async function loadTasks() {
      await store.dispatch('loadTasks');
      tasks.list = store.getters.getTasks;
    }

    watch(tasks, () => {
      if (!isCompleted.value) {
        getTasks();
      } else {
        getTasks(true);
      }
    });

    return {
      tasks,
      deleteTask,
      completeTask,
      seeCompleted,
      filteredTasks,
      seeAll,
      isCompleted,
      store,
      loadTasks
    };
  },

  beforeUnmount() {
    this.store.dispatch('setTasks', this.tasks.list);
  }
};
</script>
