<template>
  <form action="#" class="task-form" @submit.prevent="emmitSubmit">
    <input type="text" class="input" placeholder="Title" v-model="title" />
    <textarea
      cols="1"
      rows="5"
      class="input"
      placeholder="Description"
      v-model="descr"
    ></textarea>

    <button class="btn">Submit</button>
  </form>
</template>
<script>
import { ref } from 'vue';
export default {
  emits: ['save-data'],
  setup(_, context) {
    const title = ref(''),
      descr = ref('');

    function emmitSubmit() {
      const data = {
        title: title.value,
        descr: descr.value,
        done: false
      };
      context.emit('save-data', data);
    }
    return {
      title,
      descr,
      emmitSubmit
    };
  }
};
</script>
