export default {
  async registerTask(context, data) {
    const response = await fetch(
      'https://todo-9ed2d-default-rtdb.europe-west1.firebasedatabase.app/tasks.json',
      {
        method: 'POST',
        body: JSON.stringify(data)
      }
    );

    const responseData = await response.json();

    data = {
      ...data,
      id: responseData.name
    };

    context.commit('registerTask', data);
  },
  async setTasks(context, data) {
    context.commit('setTasks', data);
  },
  async loadTasks(context) {
    const response = await fetch(
      'https://todo-9ed2d-default-rtdb.europe-west1.firebasedatabase.app/tasks.json'
    );

    const responseData = await response.json();

    const tasks = [];

    for (const key in responseData) {
      tasks.unshift({
        id: key,
        title: responseData[key].title,
        descr: responseData[key].descr,
        done: responseData[key].done
      });
    }
    context.commit('setTasks', tasks);
  },
  completeTask(context, id) {
    fetch(
      `https://todo-9ed2d-default-rtdb.europe-west1.firebasedatabase.app/tasks/${id}.json`,
      {
        method: 'PATCH',
        body: JSON.stringify({
          done: true
        })
      }
    );
    context.commit('completeTask', id);
  },
  deleteTask(context, id) {
    fetch(
      `https://todo-9ed2d-default-rtdb.europe-west1.firebasedatabase.app/tasks/${id}.json`,
      {
        method: 'DELETE'
      }
    );
    context.commit('deleteTask', id);
  }
};
