<template>
  <li class="task">
    <div class="task__title title">{{ title }}</div>
    <div class="task__descr descr">{{ descr }}</div>
    <div class="task__btns btns-list">
      <router-link class="btn" :to="taskDetailLink">See more</router-link>
      <button v-if="!done" class="btn" @click="doneTask()">Done</button>
      <button class="btn btn_red" @click="deleteTask()">Delete</button>
    </div>
  </li>
</template>
<script>
import { computed } from 'vue';
import { useRoute } from 'vue-router';
export default {
  props: ['title', 'descr', 'id', 'done'],
  setup(props, context) {
    const route = useRoute();
    function deleteTask() {
      context.emit('deleteTask', props.id);
    }
    function doneTask() {
      context.emit('doneTask', props.id);
    }
    const taskDetailLink = computed(() => {
      return route.path + '/' + props.id;
    });
    return {
      deleteTask,
      doneTask,
      taskDetailLink
    };
  }
};
</script>
